
* {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-family: monospace, Microsoft Yahei;
}
a {
  text-decoration: none;
  color: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
ul,
ol {
  list-style: none;
}
em,
i {
  font-style: normal;
}
input,
select {
  outline: none;
}
.icon {
  fill: currentColor;
  overflow: hidden;
}
.btn {
  margin-right: auto;
  margin-left: auto;
  display: block;
  width: 100%;
  height: 2.5rem;
  border: none;
  background-color: #ff6030;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.5s;
}
.btn:hover {
  background-color: #f00;
}
.fade-enter-active {
  transition: all 0.15s ease;
}
.fade-leave-active {
  transition: all 0.25s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}


